import { sql } from "drizzle-orm";
import { pgTable, text, varchar, timestamp, integer, jsonb, boolean } from "drizzle-orm/pg-core";
import { createInsertSchema } from "drizzle-zod";
import { z } from "zod";
import { relations } from "drizzle-orm";

export const users = pgTable("users", {
  id: varchar("id").primaryKey().default(sql`gen_random_uuid()`),
  walletAddress: text("wallet_address").unique(),
  ensName: text("ens_name"),
  username: text("username"),
  displayName: text("display_name"), // User-friendly display name that users can set
  email: text("email"),
  githubId: text("github_id").unique(),
  githubUsername: text("github_username"),
  githubAccessToken: text("github_access_token"),
  profileImageUrl: text("profile_image_url"),
  credits: integer("credits").default(1000).notNull(), // Start with 1000 free credits
  totalCreditsUsed: integer("total_credits_used").default(0).notNull(),
  totalCreditsEarned: integer("total_credits_earned").default(1000).notNull(), // Track total earned including initial
  lastCreditGrant: timestamp("last_credit_grant").defaultNow(), // Track when last credits were granted
  referralCode: text("referral_code").unique(), // User's unique referral code
  referredBy: varchar("referred_by").references(() => users.id), // Who referred this user
  referralCount: integer("referral_count").default(0).notNull(), // Total successful referrals
  referralCreditsEarned: integer("referral_credits_earned").default(0).notNull(), // Credits earned from referrals
  createdAt: timestamp("created_at").defaultNow().notNull(),
  updatedAt: timestamp("updated_at").defaultNow().notNull(),
});

export const githubRepositories = pgTable("github_repositories", {
  id: varchar("id").primaryKey().default(sql`gen_random_uuid()`),
  userId: varchar("user_id").notNull().references(() => users.id),
  repoId: integer("repo_id").notNull(),
  name: text("name").notNull(),
  fullName: text("full_name").notNull(),
  description: text("description"),
  private: boolean("private").notNull().default(false),
  htmlUrl: text("html_url").notNull(),
  defaultBranch: text("default_branch").notNull().default("main"),
  language: text("language"),
  stargazersCount: integer("stargazers_count").notNull().default(0),
  forksCount: integer("forks_count").notNull().default(0),
  createdAt: timestamp("created_at").defaultNow().notNull(),
  updatedAt: timestamp("updated_at").defaultNow().notNull(),
});

export const auditSessions = pgTable("audit_sessions", {
  id: varchar("id").primaryKey().default(sql`gen_random_uuid()`),
  userId: varchar("user_id").references(() => users.id),
  sessionKey: text("session_key").notNull(),
  contractCode: text("contract_code").notNull(),
  contractLanguage: text("contract_language").notNull().default("solidity"),
  contractSource: text("contract_source").default("manual"), // manual, github
  githubRepoId: varchar("github_repo_id").references(() => githubRepositories.id),
  githubFilePath: text("github_file_path"),
  status: text("status").notNull().default("pending"), // pending, analyzing, completed, failed
  isPublic: boolean("is_public").notNull().default(false),
  publicTitle: text("public_title"),
  publicDescription: text("public_description"),
  tags: jsonb("tags").$type<string[]>().default([]),
  creditsUsed: integer("credits_used").default(0).notNull(), // Track credits spent on this audit
  codeComplexity: integer("code_complexity").default(1).notNull(), // 1-10 scale for credit calculation
  isPinned: boolean("is_pinned").notNull().default(false), // Pin important chats
  isArchived: boolean("is_archived").notNull().default(false), // Archive old chats
  createdAt: timestamp("created_at").defaultNow().notNull(),
  completedAt: timestamp("completed_at"),
});

export const auditResults = pgTable("audit_results", {
  id: varchar("id").primaryKey().default(sql`gen_random_uuid()`),
  sessionId: varchar("session_id").notNull().references(() => auditSessions.id),
  rawResponse: text("raw_response"),
  formattedReport: text("formatted_report"),
  vulnerabilityCount: jsonb("vulnerability_count").$type<{
    high: number;
    medium: number;
    low: number;
    info: number;
  }>(),
  securityScore: integer("security_score"),
  createdAt: timestamp("created_at").defaultNow().notNull(),
});

// Authentication nonces for secure wallet signing
export const authNonces = pgTable("auth_nonces", {
  id: varchar("id").primaryKey().default(sql`gen_random_uuid()`),
  walletAddress: text("wallet_address").notNull(),
  nonce: text("nonce").notNull().unique(),
  message: text("message").notNull(),
  expiresAt: timestamp("expires_at").notNull(),
  used: boolean("used").notNull().default(false),
  createdAt: timestamp("created_at").defaultNow().notNull(),
});

// Credit transactions table for detailed tracking
export const creditTransactions = pgTable("credit_transactions", {
  id: varchar("id").primaryKey().default(sql`gen_random_uuid()`),
  userId: varchar("user_id").notNull().references(() => users.id, { onDelete: "cascade" }),
  type: text("type", { enum: ["deduction", "purchase", "bonus", "refund", "initial", "referral"] }).notNull(),
  amount: integer("amount").notNull(), // Positive for additions, negative for deductions
  reason: text("reason").notNull(), // Description of transaction
  sessionId: varchar("session_id").references(() => auditSessions.id), // Link to audit session if applicable
  referralId: varchar("referral_id"), // Link to referral if applicable
  metadata: jsonb("metadata").$type<{
    codeLength?: number;
    complexity?: number;
    packageId?: string;
    paypalOrderId?: string;
    originalAmount?: number;
    referralCode?: string;
    referredUserId?: string;
  }>(), // Additional data
  balanceAfter: integer("balance_after").notNull(), // User's credit balance after this transaction
  createdAt: timestamp("created_at").defaultNow().notNull(),
});

// Referral tracking table
export const referrals = pgTable("referrals", {
  id: varchar("id").primaryKey().default(sql`gen_random_uuid()`),
  referrerId: varchar("referrer_id").notNull().references(() => users.id, { onDelete: "cascade" }),
  referredUserId: varchar("referred_user_id").notNull().references(() => users.id, { onDelete: "cascade" }),
  referralCode: text("referral_code").notNull(),
  status: text("status", { enum: ["pending", "completed", "credited"] }).notNull().default("pending"),
  referrerReward: integer("referrer_reward").default(0).notNull(), // Credits given to referrer
  referredReward: integer("referred_reward").default(0).notNull(), // Credits given to referred user
  completedAt: timestamp("completed_at"), // When referral requirements were met
  creditedAt: timestamp("credited_at"), // When credits were awarded
  metadata: jsonb("metadata").$type<{
    firstAuditCompleted?: boolean;
    creditsSpent?: number;
    signupSource?: string;
  }>(),
  createdAt: timestamp("created_at").defaultNow().notNull(),
});

// Credit packages for purchase
export const creditPackages = pgTable("credit_packages", {
  id: varchar("id").primaryKey().default(sql`gen_random_uuid()`),
  name: text("name").notNull(),
  credits: integer("credits").notNull(),
  bonusCredits: integer("bonus_credits").default(0).notNull(), // Extra credits for bulk purchases
  totalCredits: integer("total_credits").notNull(), // credits + bonusCredits
  price: integer("price").notNull(), // Price in cents
  popular: boolean("popular").default(false).notNull(),
  savings: integer("savings").default(0).notNull(), // Percentage savings vs individual purchase
  active: boolean("active").default(true).notNull(),
  sortOrder: integer("sort_order").default(0).notNull(),
  createdAt: timestamp("created_at").defaultNow().notNull(),
});

// Enterprise contact requests
export const enterpriseContacts = pgTable("enterprise_contacts", {
  id: varchar("id").primaryKey().default(sql`gen_random_uuid()`),
  userId: varchar("user_id").references(() => users.id),
  companyName: text("company_name").notNull(),
  contactName: text("contact_name").notNull(),
  email: text("email").notNull(),
  phone: text("phone"),
  teamSize: text("team_size"),
  monthlyAudits: text("monthly_audits"),
  requirements: text("requirements"),
  status: text("status").notNull().default("pending"), // pending, contacted, demo_scheduled, closed
  createdAt: timestamp("created_at").defaultNow().notNull(),
  updatedAt: timestamp("updated_at").defaultNow().notNull(),
});

// Live scanned contracts from blockchain explorers
export const liveScannedContracts = pgTable("live_scanned_contracts", {
  id: varchar("id").primaryKey().default(sql`gen_random_uuid()`),
  contractAddress: text("contract_address").notNull().unique(),
  network: text("network").notNull(), // ethereum, polygon, bsc, etc.
  contractName: text("contract_name"),
  contractType: text("contract_type"), // Token, DEX, DeFi, NFT, etc.
  sourceCode: text("source_code").notNull(),
  compiler: text("compiler"),
  optimization: boolean("optimization"),
  verificationDate: timestamp("verification_date"),
  auditSessionId: varchar("audit_session_id").references(() => auditSessions.id),
  scanStatus: text("scan_status").notNull().default("pending"), // pending, scanning, completed, failed
  securityScore: integer("security_score"),
  vulnerabilityCount: jsonb("vulnerability_count").$type<{
    high: number;
    medium: number;
    low: number;
    info: number;
  }>(),
  isHighProfile: boolean("is_high_profile").default(false), // Mark interesting contracts
  explorerUrl: text("explorer_url").notNull(),
  createdAt: timestamp("created_at").defaultNow().notNull(),
  scannedAt: timestamp("scanned_at"),
});

// API Keys for third-party integrations
export const apiKeys = pgTable("api_keys", {
  id: varchar("id").primaryKey().default(sql`gen_random_uuid()`),
  userId: varchar("user_id").notNull().references(() => users.id, { onDelete: "cascade" }),
  keyId: text("key_id").notNull().unique(), // Public identifier like "sa_..."
  hashedKey: text("hashed_key").notNull(), // Hashed full key for verification
  name: text("name").notNull(), // User-friendly name
  permissions: jsonb("permissions").$type<string[]>().default(["audit:read", "audit:write"]), // Scoped permissions
  lastUsed: timestamp("last_used"),
  usageCount: integer("usage_count").default(0).notNull(),
  rateLimit: integer("rate_limit").default(1000).notNull(), // Requests per hour
  active: boolean("active").default(true).notNull(),
  expiresAt: timestamp("expires_at"), // Optional expiration
  createdAt: timestamp("created_at").defaultNow().notNull(),
  updatedAt: timestamp("updated_at").defaultNow().notNull(),
});

// Webhook endpoints for real-time notifications
export const webhooks = pgTable("webhooks", {
  id: varchar("id").primaryKey().default(sql`gen_random_uuid()`),
  userId: varchar("user_id").notNull().references(() => users.id, { onDelete: "cascade" }),
  url: text("url").notNull(),
  secret: text("secret").notNull(), // For HMAC signature verification
  events: jsonb("events").$type<string[]>().default(["audit.completed", "audit.failed"]), // Event subscriptions
  active: boolean("active").default(true).notNull(),
  lastTriggered: timestamp("last_triggered"),
  successCount: integer("success_count").default(0).notNull(),
  failureCount: integer("failure_count").default(0).notNull(),
  retryCount: integer("retry_count").default(3).notNull(), // Max retry attempts
  timeoutSeconds: integer("timeout_seconds").default(30).notNull(),
  createdAt: timestamp("created_at").defaultNow().notNull(),
  updatedAt: timestamp("updated_at").defaultNow().notNull(),
});

// Webhook delivery logs for debugging and monitoring
export const webhookDeliveries = pgTable("webhook_deliveries", {
  id: varchar("id").primaryKey().default(sql`gen_random_uuid()`),
  webhookId: varchar("webhook_id").notNull().references(() => webhooks.id, { onDelete: "cascade" }),
  eventType: text("event_type").notNull(),
  payload: jsonb("payload").notNull(),
  httpStatus: integer("http_status"),
  responseBody: text("response_body"),
  responseHeaders: jsonb("response_headers").$type<Record<string, string>>(),
  attemptNumber: integer("attempt_number").default(1).notNull(),
  success: boolean("success").default(false).notNull(),
  errorMessage: text("error_message"),
  deliveredAt: timestamp("delivered_at"),
  createdAt: timestamp("created_at").defaultNow().notNull(),
});

// CI/CD integration configurations
export const cicdConfigurations = pgTable("cicd_configurations", {
  id: varchar("id").primaryKey().default(sql`gen_random_uuid()`),
  userId: varchar("user_id").notNull().references(() => users.id, { onDelete: "cascade" }),
  repositoryId: varchar("repository_id").references(() => githubRepositories.id),
  name: text("name").notNull(),
  provider: text("provider").notNull(), // github_actions, jenkins, gitlab_ci, custom
  configuration: jsonb("configuration").notNull(), // Provider-specific config
  webhookUrl: text("webhook_url"), // Webhook URL for triggers
  triggers: jsonb("triggers").$type<string[]>().default(["push", "pull_request"]), // When to trigger
  active: boolean("active").default(true).notNull(),
  lastTriggered: timestamp("last_triggered"),
  triggerCount: integer("trigger_count").default(0).notNull(),
  createdAt: timestamp("created_at").defaultNow().notNull(),
  updatedAt: timestamp("updated_at").defaultNow().notNull(),
});

// Relations
export const usersRelations = relations(users, ({ many }) => ({
  auditSessions: many(auditSessions),
  githubRepositories: many(githubRepositories),
  creditTransactions: many(creditTransactions),
  apiKeys: many(apiKeys),
  webhooks: many(webhooks),
  cicdConfigurations: many(cicdConfigurations),
}));

export const githubRepositoriesRelations = relations(githubRepositories, ({ one, many }) => ({
  user: one(users, {
    fields: [githubRepositories.userId],
    references: [users.id],
  }),
  auditSessions: many(auditSessions),
}));

export const auditSessionsRelations = relations(auditSessions, ({ one, many }) => ({
  user: one(users, {
    fields: [auditSessions.userId],
    references: [users.id],
  }),
  githubRepository: one(githubRepositories, {
    fields: [auditSessions.githubRepoId],
    references: [githubRepositories.id],
  }),
  results: many(auditResults),
  creditTransactions: many(creditTransactions),
}));

export const auditResultsRelations = relations(auditResults, ({ one }) => ({
  session: one(auditSessions, {
    fields: [auditResults.sessionId],
    references: [auditSessions.id],
  }),
}));

export const authNoncesRelations = relations(authNonces, ({ one }) => ({
  user: one(users, {
    fields: [authNonces.walletAddress],
    references: [users.walletAddress],
  }),
}));

export const creditTransactionsRelations = relations(creditTransactions, ({ one }) => ({
  user: one(users, {
    fields: [creditTransactions.userId],
    references: [users.id],
  }),
  session: one(auditSessions, {
    fields: [creditTransactions.sessionId],
    references: [auditSessions.id],
  }),
}));

export const liveScannedContractsRelations = relations(liveScannedContracts, ({ one }) => ({
  auditSession: one(auditSessions, {
    fields: [liveScannedContracts.auditSessionId],
    references: [auditSessions.id],
  }),
}));

export const apiKeysRelations = relations(apiKeys, ({ one }) => ({
  user: one(users, {
    fields: [apiKeys.userId],
    references: [users.id],
  }),
}));

export const webhooksRelations = relations(webhooks, ({ one, many }) => ({
  user: one(users, {
    fields: [webhooks.userId],
    references: [users.id],
  }),
  deliveries: many(webhookDeliveries),
}));

export const webhookDeliveriesRelations = relations(webhookDeliveries, ({ one }) => ({
  webhook: one(webhooks, {
    fields: [webhookDeliveries.webhookId],
    references: [webhooks.id],
  }),
}));

export const cicdConfigurationsRelations = relations(cicdConfigurations, ({ one }) => ({
  user: one(users, {
    fields: [cicdConfigurations.userId],
    references: [users.id],
  }),
  repository: one(githubRepositories, {
    fields: [cicdConfigurations.repositoryId],
    references: [githubRepositories.id],
  }),
}));

// Insert schemas
export const insertUserSchema = createInsertSchema(users).pick({
  walletAddress: true,
  ensName: true,
  username: true,
  displayName: true,
  email: true,
  githubId: true,
  githubUsername: true,
  githubAccessToken: true,
  profileImageUrl: true,
});

export const insertGithubRepositorySchema = createInsertSchema(githubRepositories).pick({
  userId: true,
  repoId: true,
  name: true,
  fullName: true,
  description: true,
  private: true,
  htmlUrl: true,
  defaultBranch: true,
  language: true,
  stargazersCount: true,
  forksCount: true,
});

export const insertAuditSessionSchema = createInsertSchema(auditSessions).pick({
  userId: true,
  sessionKey: true,
  contractCode: true,
  contractLanguage: true,
  contractSource: true,
  githubRepoId: true,
  githubFilePath: true,
  isPublic: true,
  publicTitle: true,
  publicDescription: true,
  tags: true,
});

export const updateAuditVisibilitySchema = createInsertSchema(auditSessions).pick({
  isPublic: true,
  publicTitle: true,
  publicDescription: true,
  tags: true,
});

export const insertEnterpriseContactSchema = createInsertSchema(enterpriseContacts).omit({
  id: true,
  status: true,
  createdAt: true,
  updatedAt: true,
});

export type InsertEnterpriseContact = z.infer<typeof insertEnterpriseContactSchema>;
export type EnterpriseContact = typeof enterpriseContacts.$inferSelect;

export const insertAuditResultSchema = createInsertSchema(auditResults).pick({
  sessionId: true,
  rawResponse: true,
  formattedReport: true,
  vulnerabilityCount: true,
  securityScore: true,
});

export const insertAuthNonceSchema = createInsertSchema(authNonces).pick({
  walletAddress: true,
  nonce: true,
  message: true,
  expiresAt: true,
});

export const insertCreditTransactionSchema = createInsertSchema(creditTransactions).pick({
  userId: true,
  type: true,
  amount: true,
  reason: true,
  sessionId: true,
  metadata: true,
  balanceAfter: true,
});

export const insertCreditPackageSchema = createInsertSchema(creditPackages).pick({
  name: true,
  credits: true,
  bonusCredits: true,
  totalCredits: true,
  price: true,
  popular: true,
  savings: true,
  active: true,
  sortOrder: true,
});

export const insertLiveScannedContractSchema = createInsertSchema(liveScannedContracts).pick({
  contractAddress: true,
  network: true,
  contractName: true,
  contractType: true,
  sourceCode: true,
  compiler: true,
  optimization: true,
  verificationDate: true,
  auditSessionId: true,
  scanStatus: true,
  securityScore: true,
  vulnerabilityCount: true,
  isHighProfile: true,
  explorerUrl: true,
});

export const insertApiKeySchema = createInsertSchema(apiKeys).pick({
  userId: true,
  keyId: true,
  hashedKey: true,
  name: true,
  permissions: true,
  rateLimit: true,
  expiresAt: true,
});

export const insertWebhookSchema = createInsertSchema(webhooks).pick({
  userId: true,
  url: true,
  secret: true,
  events: true,
  retryCount: true,
  timeoutSeconds: true,
});

export const insertWebhookDeliverySchema = createInsertSchema(webhookDeliveries).pick({
  webhookId: true,
  eventType: true,
  payload: true,
  httpStatus: true,
  responseBody: true,
  responseHeaders: true,
  attemptNumber: true,
  success: true,
  errorMessage: true,
  deliveredAt: true,
});

export const insertCicdConfigurationSchema = createInsertSchema(cicdConfigurations).pick({
  userId: true,
  repositoryId: true,
  name: true,
  provider: true,
  configuration: true,
  webhookUrl: true,
  triggers: true,
});

export const insertReferralSchema = createInsertSchema(referrals).pick({
  referrerId: true,
  referredUserId: true,
  referralCode: true,
  status: true,
  referrerReward: true,
  referredReward: true,
  metadata: true,
});

// Types
export type InsertUser = z.infer<typeof insertUserSchema>;
export type User = typeof users.$inferSelect;
export type InsertGithubRepository = z.infer<typeof insertGithubRepositorySchema>;
export type GithubRepository = typeof githubRepositories.$inferSelect;
export type InsertAuditSession = z.infer<typeof insertAuditSessionSchema>;
export type AuditSession = typeof auditSessions.$inferSelect;
export type UpdateAuditVisibility = z.infer<typeof updateAuditVisibilitySchema>;
export type InsertAuditResult = z.infer<typeof insertAuditResultSchema>;
export type AuditResult = typeof auditResults.$inferSelect;
export type InsertAuthNonce = z.infer<typeof insertAuthNonceSchema>;
export type AuthNonce = typeof authNonces.$inferSelect;
export type InsertCreditTransaction = z.infer<typeof insertCreditTransactionSchema>;
export type CreditTransaction = typeof creditTransactions.$inferSelect;
export type InsertCreditPackage = z.infer<typeof insertCreditPackageSchema>;
export type CreditPackage = typeof creditPackages.$inferSelect;
export type InsertLiveScannedContract = z.infer<typeof insertLiveScannedContractSchema>;
export type LiveScannedContract = typeof liveScannedContracts.$inferSelect;
export type InsertReferral = z.infer<typeof insertReferralSchema>;
export type Referral = typeof referrals.$inferSelect;
export type InsertApiKey = z.infer<typeof insertApiKeySchema>;
export type ApiKey = typeof apiKeys.$inferSelect;
export type InsertWebhook = z.infer<typeof insertWebhookSchema>;
export type Webhook = typeof webhooks.$inferSelect;
export type InsertWebhookDelivery = z.infer<typeof insertWebhookDeliverySchema>;
export type WebhookDelivery = typeof webhookDeliveries.$inferSelect;
export type InsertCicdConfiguration = z.infer<typeof insertCicdConfigurationSchema>;
export type CicdConfiguration = typeof cicdConfigurations.$inferSelect;
